import { useState, useEffect } from 'react';
import axios from 'axios'

import { Table, Button } from 'reactstrap';
import dayjs from 'dayjs'
import { URL_APPEND } from '@general/constants'
import Loading from '@general/loading'

import advancedFormat from 'dayjs/plugin/advancedFormat'
dayjs.extend(advancedFormat)

const BillReport = () => {
    const [data, setData] = useState([]);
    const [isLoading, setIsLoading] = useState(true);
    let trackerUrl = `${URL_APPEND}/api/tracker/list`

    useEffect(() => {
        // setData(sampledata)
        axios.get(trackerUrl)
            .then(res => {
                let remapped = res.data.map(obj => ({
                    bill_id: obj.bill,
                    title: dayjs(obj.stage_date).format('MMMM Do, YYYY'),
                    cardSubtitle: obj.bill_name,
                    cardDetailedText: obj.details
                }))
                setData(remapped)
                setIsLoading(false)

            })
            .catch(err => console.log(err));

    }, [])

    return isLoading ? <Loading /> : (
        <>
            <Button color='warning' size='sm' className='float-right' onClick={() => {
                var mywindow = window.open('', 'my div', 'height=400,width=1200');
                var printprevtimeout = 1000;
                var header = '<html><head><title>Bills Report</title>';
                header += '<link rel="stylesheet" href="https://cdn.jsdelivr.net/npm/bootstrap@4.5.3/dist/css/bootstrap.min.css" integrity="sha384-TX8t27EcRE3e/ihU7zmQxVncDAy5uIKz4rEkgIXeMed4M0jlfIDPvg6uqKI2xXr2" crossorigin="anonymous">';
                mywindow.document.write(header);

                mywindow.document.write('</head><body><div class="container-fluid">');
                mywindow.document.write(document.getElementById('rpt').innerHTML);
                mywindow.document.write('</div></body></html>');
                mywindow.document.close();
                setTimeout(function () {
                    mywindow.print();
                }, printprevtimeout);
            }}>
                <i className="fa fa-print" aria-hidden="true"></i> Print
            </Button>
            <br />
            <div id='rpt'>
            <link rel="stylesheet" href="https://maxcdn.bootstrapcdn.com/bootstrap/4.0.0/css/bootstrap.min.css" integrity="sha384-Gn5384xqQ1aoWXA+058RXPxPg6fy4IWvTNh0E263XmFcJlSAwiGgFAW/dAiS6JXm" crossorigin="anonymous" />
                <Table hover bordered>
                    <thead>
                        <tr>
                            <th>Date</th>
                            <th>Bill</th>
                            <th>Details</th>
                        </tr>
                    </thead>
                    <tbody>
                        {data && data.map(b => {
                            return <tr>
                                <td>{b.title}</td>
                                <td>{b.cardSubtitle}</td>
                                <td>{b.cardDetailedText}</td>
                            </tr>
                        })}

                    </tbody>
                </Table>

            </div>
        </>
    )
}

export default BillReport;